<template>
  <div class="mt-3">
    <section id="card-navigation">
      <b-row>
        <!-- pill tabs inside card -->
        <b-col md="12">
          <b-card
            class="text-center"
            style="
              background: #ffffff;
              box-shadow: 0px 0px 41px 9px rgba(0, 0, 0, 0.11);
              border-radius: 20px;
              height: auto;
            "
          >
            <b-card-header>
              <span
                class="saj-title m-0"
                style="
                  font-family: Poppins;
                  font-style: normal;
                  font-weight: 500;
                  line-height: 30px;
                  letter-spacing: 0.4px;
                  color: #000000;
                "
              >
                {{ $t("Part") }} 3
              </span>
            </b-card-header>

            <hr>

            <b-tabs
              nav-class="saj-title m-0"
              style="
                font-family: Poppins;
                font-style: normal;
                font-weight: 600;
                line-height: 24px;
                letter-spacing: 0.1em;
              "
            >
              <!-- Latihan dan Pembangunan -->

              <b-tab
                :title="$t('3a. Training and Development')"
                active
              >
                <!-- <div
                  v-if="roles.isTopManagement"
                  class="saj-title mt-1 mr-1 mb-3 d-flex justify-content-end"
                >
                  {{
                    training.total_score !== null ? training.total_score : 0
                  }}/{{ weightages.training + weightages.self_learning }}
                  %
                </div> -->
                <div
                  class="saj-title mt-1 mr-1 mb-3 d-flex justify-content-end"
                  v-if="training.hasTraining == 1"
                >
                  {{
                    training.total_score !== null ? training.total_score : 0
                  }}/{{ weightages.training + weightages.self_learning }}
                  %
                </div>
                <div
                  fluid
                  class="bv-example-row"
                >
                  <div
                    v-if="
                      training.submit_at === null
                    "
                    class="d-flex justify-content-end"
                  >
                    <b-col
                      style="display: flex; justify-content: flex-end"
                    ><b-button
                      variant="primary"
                      class="saj-title mb-1"
                      style="margin-right: 10px"
                      @click="addTraining()"
                    >
                      {{
                        training.hasTraining == 1
                          ? $t("No Training Attended")
                          : $t("Add Training")
                      }}
                    </b-button>
                    </b-col>
                  </div>
                  <div v-if="training.hasTraining == 1">
                    <div
                      v-if="status.toLowerCase() !== 'completed'"
                      class="d-flex justify-content-end"
                    >
                      <b-col
                        style="display: flex; justify-content: flex-end"
                      ><b-button
                         :style="approved_at === null ? '' : 'display: none;'"
                         variant="primary"
                         class="saj-title"
                         style="margin-right: 10px"
                         @click="isEditTrain = !isEditTrain"
                       >
                         {{ isEditTrain ? $t("Cancel") : $t("Edit") }}
                       </b-button>
                        <b-button
                          v-if="isEditTrain"
                          class="saj-title"
                          style="
                            margin-right: 9px;
                            background: #0b103c !important;
                            border-color: #0b103c !important;
                          "
                          @click="validateForm()"
                        >
                          {{ $t("Save") }}
                        </b-button>
                        <b-button
                          v-if="isSaveTraining"
                          class="saj-title"
                          style="
                            margin-right: 9px;
                            background: #0b103c !important;
                            border-color: #0b103c !important;
                          "
                        >
                          <i class="fa fa-spinner fa-spin" /> {{ $t("Saving") }}
                        </b-button>
                      </b-col>
                    </div>

                    <div
                      class="saj-text m-0 ml-1 mt-2 mb-2 p-0"
                      style="
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        line-height: 24px;
                        letter-spacing: 0.4px;
                        color: #000000;
                        text-align: start !important;
                      "
                    >
                      {{
                        $t(
                          "List The Program and Training That Involved during the evaluation period"
                        )
                      }}.
                    </div>
                    <b-col
                      v-if="roles.isTopManagement"
                      cols="12"
                      class="justify-content-end m-0 p-0"
                    >
                      <div
                        class="saj-title mt-2 mr-1 mb-1 d-flex justify-content-end"
                      >
                        {{ training.program_score }}/{{ weightages.training + weightages.self_learning }} %
                      </div>
                    </b-col>
                    <div class="m-2">
                      <b-form
                        ref="form"
                        :style="{ height: trHeight }"
                        class="repeater-form"
                        @submit.prevent="repeateAgain"
                      >
                        <!-- Row Loop -->
                        <template v-for="(training, i) in trainingList">
                          <validation-observer ref="part3ValidationUpdate">
                            <b-row
                              :id="training.id"
                              :key="'a' + i"
                              ref="row"
                              class="m-0 p-2 mb-2"
                              style="
                                color: black;
                                background: #f7f7fc;
                                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25),
                                  0px 0px 41px 9px rgba(0, 0, 0, 0.11);
                                border-radius: 20px;
                                height: 100%;
                              "
                            >
                              {{ i + 1 + "." }}

                              <!-- Tajuk Kursus / Program -->

                              <div class="saj-title col-md-4">
                                <validation-provider
                                  #default="{ errors }"
                                  :name="$t('Program Title')"
                                  :rules="'required'"
                                >
                                  <b-form-group
                                    label-size="lg"
                                    :label="$t('Program Title')"
                                    label-for="tajuk-kursus"
                                  >
                                    <b-form-input
                                      v-model="training.program_name"
                                      class="saj-text"
                                      type="text"
                                      :disabled="!isEditTrain"
                                      placeholder=""
                                    />
                                  </b-form-group>
                                  <small class="text-danger">{{
                                    errors[0]
                                  }}</small>
                                </validation-provider>
                              </div>

                              <!-- Tarikh Mula -->
                              <div class="saj-title col-md-2">
                                <b-form-group
                                  label-size="lg"
                                  :label="$t('Start Date')"
                                  label-for="mula"
                                >
                                  <validation-provider
                                    #default="{ errors }"
                                    :name="$t('Start Date')"
                                    :rules="'required'"
                                  >
                                    <div
                                      :style="
                                        !isEditTrain
                                          ? `background-color: #efefef; border-radius: 0.357rem`
                                          : `background-color: white; border-radius: 0.357rem`
                                      "
                                    >
                                      <flat-pickr
                                        v-model="training.start_date"
                                        class="form-control saj-text"
                                        :config="{
                                          dateFormat: 'Y-m-d',
                                          altInput: true,
                                          altFormat: 'd-m-Y',
                                        }"
                                        :disabled="!isEditTrain"
                                      />
                                    </div>
                                    <small class="text-danger">{{
                                      errors[0]
                                    }}</small>
                                  </validation-provider>
                                </b-form-group>
                              </div>

                              <!-- Tarikh Akhir -->
                              <div class="saj-title col-md-2">
                                <b-form-group
                                  label-size="lg"
                                  :label="$t('End Date')"
                                  label-for="akhir"
                                >
                                  <validation-provider
                                    #default="{ errors }"
                                    :name="$t('End Date')"
                                    :rules="'required'"
                                  >
                                    <div
                                      :style="
                                        !isEditTrain
                                          ? `background-color: #efefef; border-radius: 0.357rem`
                                          : `background-color: white; border-radius: 0.357rem`
                                      "
                                    >
                                      <flat-pickr
                                        v-model="training.end_date"
                                        class="form-control saj-text"
                                        :config="{
                                          dateFormat: 'Y-m-d',
                                          altInput: true,
                                          altFormat: 'd-m-Y',
                                        }"
                                        :disabled="!isEditTrain"
                                      />
                                    </div>
                                    <small class="text-danger">{{
                                      errors[0]
                                    }}</small>
                                  </validation-provider>
                                </b-form-group>
                              </div>

                              <!-- Number of hours -->
                              <div class="saj-title col-md-3 mr-1">
                                <validation-provider
                                  #default="{ errors }"
                                  :name="$t('Number of Hours')"
                                  :rules="'required'"
                                >
                                  <b-form-group
                                    label-size="lg"
                                    :label="$t('Number of hours')"
                                    label-for="jumlah-jam"
                                  >
                                    <b-form-input
                                      v-model="training.hour"
                                      class="saj-text"
                                      type="number"
                                      :disabled="!isEditTrain"
                                      placeholder=""
                                    />
                                  </b-form-group>
                                  <small class="text-danger">{{
                                    errors[0]
                                  }}</small>
                                </validation-provider>
                              </div>

                              <!-- Add and Remove Button -->
                              <div v-if="isEditTrain">
                                <feather-icon
                                  ref="training-items"
                                  style="
                                    width: 30px;
                                    height: 30px;
                                    color: #c5c7cd;
                                    margin-top: 50px;
                                  "
                                  icon="MinusCircleIcon"
                                  @click="
                                    showAlertModal('training', training.id)
                                  "
                                />
                              </div>
                              <b-row
                                class="saj-text d-flex justify-content-start align-items-center m-0 ml-3"
                                style="
                                  font-family: 'Poppins';
                                  font-style: normal;
                                  font-weight: 600;
                                  line-height: 24px;
                                  letter-spacing: 0.1em;
                                  color: #000000;
                                  width: 100%;
                                "
                              >
                                <div
                                  v-if="training.file === null"
                                  class="saj-text row align-items-center"
                                  style=""
                                >
                                  <feather-icon
                                    class="mr-1"
                                    icon="PaperclipIcon"
                                    size="16"
                                  />
                                  {{ $t("No File") }}
                                </div>
                                <div
                                  v-if="training.file !== null"
                                  class="saj-text row align-items-center"
                                  style=""
                                >
                                  <feather-icon
                                    class="mr-1"
                                    icon="PaperclipIcon"
                                    size="16"
                                  />
                                  <a
                                    :href="training.file.url"
                                    target="_blank"
                                  >
                                    {{ training.file.name }}
                                  </a>
                                </div>
                              </b-row>
                              <b-row
                                v-if="isEditTrain"
                                class="col-12 p-1 m-0 d-flex justify-content-start"
                              >
                                <b-col>
                                  <b-form-file
                                    v-model="training.fileToUpload"
                                    type="file"
                                    :disabled="!isEditTrain"
                                    class="saj-text mb-1 mt-1 d-flex justify-content-start"
                                    :browse-text="$t('Choose File')"
                                    :placeholder="$t('No file choosen')"
                                    style="width: 30%; text-align: start"
                                    @change="onFileChange"
                                  />
                                </b-col>
                              </b-row>
                              <b-row
                                class="self-score d-flex justify-content-end align-items-center m-0 col-12"
                                style="
                                  font-family: 'Poppins';
                                  font-style: normal;
                                  font-weight: 600;
                                  line-height: 24px;
                                  letter-spacing: 0.1em;
                                  color: #000000;
                                  width: 100%;
                                "
                              >
                                {{ $t("Self Score") }}:
                                <validation-provider
                                  #default="{ errors }"
                                  :name="$t('Self Score')"
                                  :rules="'required'"
                                >
                                  <b-form-rating
                                    v-model="training.self_score"
                                    show-clear
                                    style="background: none !important"
                                    no-border
                                    show-value
                                    inline
                                    color="#DFB300"
                                    stars="6"
                                    size="lg"
                                    :disabled="!isEditTrain"
                                  />
                                  <small class="text-danger">{{
                                    errors[0]
                                  }}</small>
                                </validation-provider>
                                <span
                                  style="margin-top: 5px; margin-right: 15px"
                                >
                                  <h5 v-if="training.self_score == '1'">
                                    {{
                                      isEnglish
                                        ? rating[5].definition_en
                                        : rating[5].definition_my
                                    }}
                                  </h5>
                                  <h5 v-else-if="training.self_score == '2'">
                                    {{
                                      isEnglish
                                        ? rating[4].definition_en
                                        : rating[4].definition_my
                                    }}
                                  </h5>
                                  <h5 v-else-if="training.self_score == '3'">
                                    {{
                                      isEnglish
                                        ? rating[3].definition_en
                                        : rating[3].definition_my
                                    }}
                                  </h5>
                                  <h5 v-else-if="training.self_score == '4'">
                                    {{
                                      isEnglish
                                        ? rating[2].definition_en
                                        : rating[2].definition_my
                                    }}
                                  </h5>
                                  <h5 v-else-if="training.self_score == '5'">
                                    {{
                                      isEnglish
                                        ? rating[1].definition_en
                                        : rating[1].definition_my
                                    }}
                                  </h5>
                                  <h5 v-else-if="training.self_score == '6'">
                                    {{
                                      isEnglish
                                        ? rating[0].definition_en
                                        : rating[0].definition_my
                                    }}
                                  </h5>
                                </span>
                              </b-row>
                              <b-row
                                class="self-score d-flex justify-content-end align-items-center m-0 col-12"
                                style="
                                  font-family: 'Poppins';
                                  font-style: normal;
                                  font-weight: 600;
                                  line-height: 24px;
                                  letter-spacing: 0.1em;
                                  color: #000000;
                                  width: 100%;
                                "
                              >
                                <div class="mr-1">
                                  {{ $t("Manager Score") }}:
                                  {{ training.manager_score }}/6
                                </div>
                              </b-row>
                              <b-col cols="12">
                                <hr>
                              </b-col>
                            </b-row>
                          </validation-observer>
                        </template>
                      </b-form>
                    </div>
                    <div class="m-2">
                      <b-form
                        ref="form"
                        :style="{ height: trHeight }"
                        class="repeater-form"
                        @submit.prevent="repeateAgain"
                      >
                        <template v-for="(item, idx) in items">
                          <!-- Row Loop -->
                          <validation-observer ref="part3ValidationNew">
                            <b-row
                              :id="item.id"
                              :key="idx"
                              ref="row"
                              class="m-0 p-2 mb-2"
                            >
                              <!-- Tajuk Kursus / Program -->
                              <div
                                class="saj-title col-md-4"
                                style="margin-left: 12px"
                              >
                                <validation-provider
                                  #default="{ errors }"
                                  :name="$t('Program Title')"
                                  :rules="idx !== 0 ? 'required' : ''"
                                >
                                  <b-form-group
                                    label-size="lg"
                                    :label="$t('Program Title')"
                                    label-for="tajuk-kursus"
                                    class=""
                                  >
                                    <b-form-input
                                      v-model="item.program_name"
                                      class="saj-text"
                                      type="text"
                                      :disabled="!isEditTrain"
                                      placeholder=""
                                    /> </b-form-group><small class="text-danger">{{
                                    errors[0]
                                  }}</small>
                                </validation-provider>
                              </div>

                              <!-- Tarikh Mula -->
                              <div class="saj-title col-md-2">
                                <b-form-group
                                  label-size="lg"
                                  :label="$t('Start Date')"
                                  label-for="mula"
                                  class=""
                                >
                                  <div
                                    class=""
                                    :class="
                                      !isEditTrain ? 'addItem' : 'addItem2'
                                    "
                                  >
                                    <flat-pickr
                                      v-model="item.tempStart_date"
                                      :config="{
                                        dateFormat: 'Y-m-d',
                                        altInput: true,
                                        altFormat: 'd-m-Y',
                                      }"
                                      :disabled="!isEditTrain"
                                      @input="setStartDate(idx)"
                                    />
                                  </div>
                                </b-form-group>
                                <validation-provider
                                  #default="{ errors }"
                                  :name="$t('Start Date')"
                                  :rules="
                                    idx !== 0 || item.program_name.length > 0
                                      ? 'required'
                                      : ''
                                  "
                                >
                                  <input
                                    v-model="item.start_date"
                                    type="hidden"
                                  >

                                  <small class="text-danger">{{
                                    errors[0]
                                  }}</small>
                                </validation-provider>
                              </div>

                              <!-- Tarikh Akhir -->
                              <div class="saj-title col-md-2">
                                <b-form-group
                                  label-size="lg"
                                  :label="$t('End Date')"
                                  label-for="akhir"
                                  class=""
                                >
                                  <div
                                    class=""
                                    :class="
                                      !isEditTrain ? 'addItem' : 'addItem2'
                                    "
                                  >
                                    <flat-pickr
                                      v-model="item.tempEnd_date"
                                      :config="{
                                        dateFormat: 'Y-m-d',
                                        altInput: true,
                                        altFormat: 'd-m-Y',
                                      }"
                                      :disabled="!isEditTrain"
                                      @input="setEndDate(idx)"
                                    />
                                  </div>
                                </b-form-group>
                                <validation-provider
                                  #default="{ errors }"
                                  :name="$t('End Date')"
                                  :rules="
                                    idx !== 0 || item.program_name.length > 0
                                      ? 'required'
                                      : ''
                                  "
                                >
                                  <input
                                    v-model="item.end_date"
                                    type="hidden"
                                  >
                                  <small class="text-danger">{{
                                    errors[0]
                                  }}</small>
                                </validation-provider>
                              </div>

                              <!-- Jumlah Jam -->
                              <div class="saj-title col-md-3 mr-1">
                                <validation-provider
                                  #default="{ errors }"
                                  :name="$t('Number of Hours')"
                                  :rules="
                                    idx !== 0 || item.program_name.length > 0
                                      ? 'required'
                                      : ''
                                  "
                                >
                                  <b-form-group
                                    label-size="lg"
                                    :label="$t('Number of hours')"
                                    label-for="jumlah-jam"
                                  >
                                    <b-form-input
                                      v-model="item.hour"
                                      class="saj-text"
                                      type="number"
                                      :disabled="!isEditTrain"
                                      placeholder=""
                                    />
                                  </b-form-group>
                                  <small class="text-danger">{{
                                    errors[0]
                                  }}</small>
                                </validation-provider>
                              </div>

                              <!-- Add and Remove Button -->
                              <div
                                v-if="isEditTrain"
                                class="mt-2 p-0 d-flex align-items-center justify-content-around"
                              >
                                <feather-icon
                                  v-model="tambah"
                                  style="
                                    width: 30px;
                                    height: 30px;
                                    color: #c5c7cd;
                                  "
                                  icon="PlusCircleIcon"
                                  @click="repeateAgain"
                                />
                                <feather-icon
                                  v-if="idx !== 0"
                                  style="
                                    width: 30px;
                                    height: 30px;
                                    color: #c5c7cd;
                                  "
                                  icon="MinusCircleIcon"
                                  @click="removeItem(idx)"
                                />
                              </div>
                              <validation-provider
                                #default="{ errors }"
                                :name="$t('File')"
                                :rules="
                                  idx !== 0 || item.program_name.length > 0
                                    ? 'required'
                                    : ''
                                "
                              >
                                <b-row
                                  class="col-12 p-1 m-0 d-flex justify-content-start"
                                >
                                  <b-form-file
                                    v-model="item.fileToUpload"
                                    type="file"
                                    class="saj-text m-0 ml-1 d-flex justify-content-start"
                                    :disabled="!isEditTrain"
                                    :browse-text="$t('Choose File')"
                                    :placeholder="$t('No file choosen')"
                                    style="width: 100%; text-align: start"
                                    @change="onFileChange"
                                  />
                                </b-row>
                                <small class="text-danger">{{
                                  errors[0]
                                }}</small>
                              </validation-provider>
                              <b-row
                                class="self-score d-flex justify-content-end align-items-center m-0 col-12"
                                style="
                                  font-family: 'Poppins';
                                  font-style: normal;
                                  font-weight: 600;
                                  line-height: 24px;
                                  letter-spacing: 0.1em;
                                  color: #000000;
                                  width: 100%;
                                "
                              >
                                {{ $t("Self Score") }}:
                                <validation-provider
                                  #default="{ errors }"
                                  :name="$t('Self Score')"
                                  :rules="
                                    idx !== 0 || item.program_name.length > 0
                                      ? 'required'
                                      : ''
                                  "
                                >
                                  <b-form-rating
                                    v-model="item.self_score"
                                    show-clear
                                    style="background: none !important"
                                    no-border
                                    show-value
                                    inline
                                    color="#DFB300"
                                    stars="6"
                                    size="lg"
                                    :disabled="!isEditTrain"
                                  />
                                  <small class="text-danger">{{
                                    errors[0]
                                  }}</small>
                                </validation-provider>
                                <span
                                  style="margin-top: 5px; margin-right: 15px"
                                >
                                  <h5 v-if="item.self_score == '1'">
                                    {{
                                      isEnglish
                                        ? rating[5].definition_en
                                        : rating[5].definition_my
                                    }}
                                  </h5>
                                  <h5 v-else-if="item.self_score == '2'">
                                    {{
                                      isEnglish
                                        ? rating[4].definition_en
                                        : rating[4].definition_my
                                    }}
                                  </h5>
                                  <h5 v-else-if="item.self_score == '3'">
                                    {{
                                      isEnglish
                                        ? rating[3].definition_en
                                        : rating[3].definition_my
                                    }}
                                  </h5>
                                  <h5 v-else-if="item.self_score == '4'">
                                    {{
                                      isEnglish
                                        ? rating[2].definition_en
                                        : rating[2].definition_my
                                    }}
                                  </h5>
                                  <h5 v-else-if="item.self_score == '5'">
                                    {{
                                      isEnglish
                                        ? rating[1].definition_en
                                        : rating[1].definition_my
                                    }}
                                  </h5>
                                  <h5 v-else-if="item.self_score == '6'">
                                    {{
                                      isEnglish
                                        ? rating[0].definition_en
                                        : rating[0].definition_my
                                    }}
                                  </h5>
                                </span>
                              </b-row>
                            </b-row>
                          </validation-observer>
                        </template>
                      </b-form>
                    </div>
                    <!-- Self Learning -->
                    <div
                      v-if="roles.isTopManagement"
                      class="mt-5"
                      style="
                        background: rgb(239, 237, 238);
                        border-radius: 20px;
                      "
                    >
                      <b-row
                        class=""
                        style="
                          letter-spacing: 0.4px;
                          color: #000000;
                          padding-top: 27px;
                        "
                      >
                        <b-col
                          cols="10"
                          class=""
                        >
                          <h1
                            class="ml-1 d-flex justify-content-start saj-title"
                          >
                            {{ $t("Self Learning") }}.
                          </h1>
                        </b-col>
                        <b-col
                          cols="2"
                          class="justify-content-end"
                        >
                          <div
                            class="saj-title mr-1 mb-1 d-flex justify-content-end"
                          >
                            {{ training.self_learn_score }}/{{
                              weightages.self_learning
                            }}
                            %
                          </div>
                        </b-col>
                      </b-row>
                      <div class="m-2 text-center">
                        <b-form
                          ref="form"
                          :style="{ height: trHeight }"
                          class="repeater-form"
                          @submit.prevent="repeateAgain4"
                        >
